@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #4D4D4D;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  text-align: center;
}

h1 {
  color: #fff;
  margin-bottom: 0.5rem;
  font-size: 1.75rem;
}

.TaskWrapper {
  background: #333333;
  margin-top: 5rem;
  padding: 2rem;
  border-radius: 20px;
  border: 1px solid orange
}

.TaskForm {
  width: 100%;
  margin-bottom: 1rem;
}

.task-input {
  outline: none;
  background: none;
  border: 1px solid orange;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
  width: 300px;
  color: #fff;
  border-radius: 5px 0 0 5px;
  };


.task-input::placeholder {
  color: #ffffff4d;
}

.task-btn {
  background: orange;
  color: #fff;
  border: none;
  padding: 0.55rem;
  cursor: pointer;
  border-radius: 0 5px 5px 0;
}

.Task {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: orange;
  color: #fff;
  padding: 0.75rem 1rem;
  border-radius: 20px;
  margin-bottom: 1rem;
}

.fa-trash {
  margin-left: 0.75rem;
}

.incompleted {
  color: #fff;
  cursor: pointer;
}

.completed {
  color: #c5aeff;
  text-decoration: line-through;
  cursor: pointer;
}

.edit-icon,
.delete-icon {
  cursor: pointer;
}
 